import {useState, useEffect} from 'react'
import {Table} from 'react-bootstrap';
import axios from 'axios';
import AddACModal from './AddACModal';
import EditACModal from './EditACModal';

function Aircraft() {
  //function component for aircaft
  //Add Constants
    const url = 'http://127.0.0.1:8000/aircraft';
    const [data, setData] = useState([]);
          
        //useEffect(() => {
            //fetch("http://127.0.0.1:8000/aircraft")
            //.then(response=>response.json())
            //.then(data=>{
                //setState({deps:data});
            //})});
      //Use Effect for getting URL and setData to json data from Django url
            useEffect(() => {

                axios.get(url)
                .then(json => 
                setData(json.data))
            }, [])

    //Contant to produce table. Starts with rendering the table.
    const renderTable = () => {
     //data is array. Map out array as per ac function. 
        return data.map(ac => {
      return (
        <tr key={ac.AircraftId}>
          <td>{ac.AircraftId}</td>
          <td>{ac.AircraftName}</td>
          <td>{ac.FlightCounts}</td>        
          <td>{ac.FirstFlight}</td>
          <td><EditACModal /></td>                
        </tr>
      )
    })
    }


    //Map out Table headers.
return(
        <div >
                <Table className="mt-4" striped bordered hover size="sm" >
                    <thead>
                        <tr>
                        <th>Aircraft Id</th>
                        <th>Aircraft Name</th>
                        <th>Flight Counts</th>
                        <th>First Flight</th>
                        <th>Options</th>
                        </tr>
                    </thead>
                    <tbody>
                    {renderTable()}
                    </tbody>
                
                </Table>
                
                <AddACModal />
                
            </div>
  );
}

export default Aircraft