import {useState, useEffect} from 'react'
import {Table} from 'react-bootstrap';
import axios from 'axios';

function Engine() {
    const url = 'http://127.0.0.1:8000/engine';
    const [data, setData] = useState([]);
          
        //useEffect(() => {
            //fetch("http://127.0.0.1:8000/engine")
            //.then(response=>response.json())
            //.then(data=>{
                //setState({deps:data});
            //})});
        
            useEffect(() => {

                axios.get(url)
                .then(json => 
                setData(json.data))
            }, [])

    
    const renderTable = () => {
    
        return data.map(eg => {
      return (
        <tr key={eg.EngineId}>
          <td>{eg.EngineId}</td>
          <td>{eg.EngineName}</td>
          <td>{eg.Aircraft}</td>        
          <td>{eg.EngineHrs}</td>                
        </tr>

      )
    })
    }
return(
        <div >
                <Table className="mt-4" striped bordered hover size="sm">
                    <thead>
                        <tr>
                        <th>Engine Id</th>
                        <th>Engine Name</th>
                        <th>Aircraft</th>
                        <th>Engine Hrs</th>
                        </tr>
                    </thead>
                    <tbody>
                    {renderTable()}
                    </tbody>

                </Table>


            </div>
  );
}

export default Engine 
