import {useState, useEffect} from 'react';
import {ButtonToolbar,Modal,Button, Row, Col, Form,Table} from 'react-bootstrap';


//Need this is for class
//export class AddDepModal extends Component{
    //constructor(props){
        //super(props);
        //this.handleSubmit=this.handleSubmit.bind(this);
   // }

const EditACModel = () => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [AircraftId,setID]=useState("");
    const [AircraftName,setName]=useState("");
    const [FlightCounts,setHrs]=useState("");
    const [FirstFlight,setFF]=useState("");

//specify link in constant
    const url = 'http://127.0.0.1:8000/aircraft'

    const handleSubmit = (e) => {
                    
        e.preventDefault();
        let data = { AircraftId, AircraftName, FlightCounts, FirstFlight}
 
      fetch(url,{

            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data
            )
        })
        //creating response to change to JSON
        .then(resp=>resp.json())
        //Providing alert result from Django views file
        .then((result)=>{
            alert(result);
            
        })
    }

return (
    <div className="container">
 <ButtonToolbar>
    <Button variant="info" onClick={handleShow}>Edit</Button>
      <Button variant="danger">Delete</Button>
<br></br>
                </ButtonToolbar>
<Modal
    show={show}
    size="lg"
    aria-labelledby="contained-modal-title-vcenter"
    centered
    >
        <Modal.Header>
            <Modal.Title id="contained-modal-title-vcenter">
                Edit Aircraft ID
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Row>
                <Col sm={6}>
                    <Form>
                        <Form.Group controlId="AircraftName">
                            <Form.Label>Aircraft Id</Form.Label>
                            <Form.Control type="text" name="AircraftId" required 
                            placeholder="Aircraft ID" onChange={(e)=>{setID(e.target.value)}} />
                            <Form.Label>Aircraft Name</Form.Label>
                            <Form.Control type="text" name="AircraftName" required 
                            placeholder="Aircraft Name" onChange={(e)=>{setName(e.target.value)}}/>
                            <Form.Label>First Flight</Form.Label>
                            <Form.Control type="text" name="FlightCounts" required 
                            placeholder="Flight Hrs" onChange={(e)=>{setHrs(e.target.value)}}/>
                            <Form.Label>First Flight</Form.Label>
                            <Form.Control type="text" name="FirstFlight" required 
                            placeholder="First Flight" onChange={(e)=>{setFF(e.target.value)}}/>
                            <br></br>
                        </Form.Group>
    
                        <Form.Group>
                            <Button variant="primary" type="submit" onClick={handleSubmit,handleClose}>
                                Edit Aircraft
                            </Button>
                            
                        </Form.Group>
                    </Form>
                </Col>
            </Row>
        </Modal.Body>
        
        <Modal.Footer>
            <Button variant="danger" onClick={handleClose}>Close</Button>
        </Modal.Footer>
    
    </Modal>
    
                </div>
        )
    }

export default EditACModel
